<script setup>
import MainLayout from '../components/MainLayout.vue';
import { db } from '../firebase';
import { ref, onMounted, computed } from 'vue';
import { collection, addDoc, serverTimestamp, onSnapshot, orderBy, query, doc, deleteDoc, updateDoc, getDocs } from 'firebase/firestore';

const task = ref('');
const todos = ref([]);
const searchQuery = ref('');
const status = ref(false);
const name1 = ref('');
const name2 = ref('');
const result = ref('');

const capitalizeFirstLetter = (str) => {
    return str.charAt(0).toUpperCase() + str.slice(1);
};
const calculateFlames = () => {
      // Function to calculate FLAMES
      const name1Chars = name1.value.toLowerCase().replace(/\s/g, '');
      const name2Chars = name2.value.toLowerCase().replace(/\s/g, '');

      const commonChars = [...name1Chars].filter((char) => name2Chars.includes(char));

      const totalChars = name1Chars.length + name2Chars.length - 2 * commonChars.length;

      const flamesOrder = 'flames';

      let flamesCount = totalChars % flamesOrder.length;

      if (flamesCount === 0) {
        flamesCount = flamesOrder.length;
      }

      result.value = flamesOrder[flamesCount - 1];
};
const addTask = async (e) => {
      e.preventDefault();
      try {
        await addDoc(collection(db, 'todo'), {
          details: task.value,
          status: status.value,
          date_created: serverTimestamp(),
        });
        task.value = '';
      } catch (e) {
        console.error('Error adding document: ', e);
      }
};

const getData = () => {
      const fsquery = query(collection(db, "todo"), orderBy('date_created','asc'));
      onSnapshot(fsquery,(querySnapshot) => {
        const newTasks = [];
        querySnapshot.forEach((doc) => {
          const datas = {
              id:doc.id,
              details:doc.data().details,
              status:doc.data().status,
            }
            newTasks.unshift(datas);
        });
        todos.value=newTasks;
      })
};

const updateData = (id, currentStatus) => {
      const fsquery = doc(db, 'todo', id);
      const newData = {
        status: !currentStatus,
      }
      updateDoc(fsquery, newData)
      .then(() => {
        console.log('Document successfully updated.');
      })
      .catch((error) => {
        console.error('Error updating document:', error);
      });
};

const removeTask = async (id) => {
      try{
        await deleteDoc(doc(db,'todo',id))
      }catch(e){
        console.log(e);
      }
};

const hasTasks = computed(()=> {
      return todos.value.length > 0;
});

const clearAllTasks = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, 'todo'));
        querySnapshot.forEach(async (doc) => {
          await deleteDoc(doc.ref);
        });
        todos.value = [];
      } catch (e) {
        console.log(e);
      }
};
const filteredTodos = computed(()=> {
      return todos.value.filter(todo => {
        return todo.details.toLowerCase().includes(searchQuery.value.toLowerCase());
      });
});

onMounted(()=> {
      getData()
})
// Page Title of Views
const pageTitle = 'Projects'
</script>

<template>
  <MainLayout :pageTitle="pageTitle">
    <section class="flex flex-col text-center w-full h-full">
      <div class="grid md:grid-cols-2 lg:grid-cols-2 grid-col-1">
        <div class="mb-4 w-full max-h-full flex flex-col items-center justify-between mx-auto md:px-4 pt-2 mt-2">
          <div class="flex-col items-center w-full h-full">
              <div class="md:pt-20 md:pb-40">
                  <div div class="h-full w-full flex items-center justify-center bg-teal-lightest bg-blue-400">
                      <div class="transparent rounded shadow p-6  w-full lg:max-w-full h-full">
                          <div class="mb-4">
                              <h1 class="text-white font-bold text-[20px] text-center">Todo List</h1>
                              <div class="flex mt-4">
                                  <input v-model="task" class="appearance-none  rounded w-full py-2 px-3 mr-4 text-center" placeholder="Add Todo">
                                  <button @click="addTask" class="flex-no-shrink p-2 border-2 rounded text-gray-400 border-teal hover:bg-blue-400 hover:text-white bg-white">Add</button>
                              </div>
                              <div class="flex flex-col items-center my-4">
                                  <input type="text" v-model="searchQuery" placeholder="Search" id="search" class="ml-2 rounded-2xl text-center"> 
                              </div>
                          </div>
                          <div class="flex flex-col mb-4">
                              <div class="flex mb-2 items-center header w-full h-10 bg-white text-gray-400 text-center">
                                  <p class="flex-1 py-6">Details</p>
                                  <p class="flex-1 py-6">Action</p>
                              </div>
                              <div class="flex items-center header h-full w-full bg-white text-gray-400 text-center" v-for="todo in filteredTodos" :key="todo.id">
                                  <div class="datas flex w-full h-full py-2">
                                      <p class="flex-1" :class="{ check: todo.status }">{{ capitalizeFirstLetter(todo.details) }}</p>
                                      <p class="flex-1">
                                          <a href="#" class="mx-2" @click="updateData(todo.id, todo.status)">
                                              <i class="bi bi-pencil-square"></i>
                                          </a>
                                          <a href="#" @click="removeTask(todo.id)">
                                              <i class="bi bi-trash3"></i>
                                          </a>
                                      </p>
                                  </div>
                              </div>
                          </div>
                          <div class="flex mt-4 text-center bg-white">
                              <button class="items-center header border w-full h-10 text-gray-400 text-center hover:text-white hover:bg-blue-400 hover:border-white" 
                                  v-if="hasTasks" @click="clearAllTasks">
                                  Clear Task
                              </button>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
        </div>
        <div class="mb-4 w-full max-h-full flex flex-col items-center justify-between mx-auto md:px-4 pt-2 mt-2">
          <div class="flex-col items-center w-full h-full">
              <div class="md:pt-20 md:pb-40">
                  <div div class="h-full w-full flex items-center justify-center bg-teal-lightest bg-blue-400">
                      <div class="transparent rounded shadow p-6  w-full lg:max-w-full h-full">
                          <div class="mb-4">
                              <h1 class="text-white font-bold text-[20px] text-center">FLAMES</h1>
                              <div class="flex mt-4">
                                  <label for="name1" class="text-white">Enter Name 1:</label>
                                  <input v-model="name1" class="appearance-none  rounded w-full py-2 px-3 mr-4 text-center">
                              </div>
                              <div class="flex mt-4">
                                <label for="name2" class="text-white">Enter Name 2:</label>
                                <input v-model="name2" class="appearance-none  rounded w-full py-2 px-3 mr-4 text-center">
                              </div>                                          
                          </div>
                          <div class="flex flex-col mb-4" v-if="result">
                            <div class="flex mb-2 items-center header w-full h-10 bg-white text-black text-center">
                                <p class="flex-1 py-6">Result: {{ result }}</p>
                            </div>
                          </div>
                          <div class="flex mt-4 text-center rounded-2xl bg-white">
                            <button @click="calculateFlames" class="items-center header border rounded-2xl w-full h-10 text-gray-400 text-center hover:text-white hover:bg-blue-400 hover:border-white" >
                                Calculate
                            </button>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
      </div>
    </section>
  </MainLayout>
</template>
<style scoped>
.check {
    text-decoration: line-through;
    color: red;
}
</style>